  //utilities
.font-family-lato{
  font-family: 'Lato', sans-serif;
}
.font-size-xs{
  font-size: $font-size-xs;
}
.font-size-base{
  font-size: $font-size-base;
}
.font-size-sm{
  font-size: $font-size-sm;
}
.font-size-lg{
  font-size: $font-size-lg;
}
.color-dark-800{
  color: $gray-800;
}
.color-dark{
  color: $dark;
}
.color-notice{
  color: $notice;
}
.bg-color-primary{
  background-color: $primary;
}

.hr{
  border-color: $border-color;
}
.border{
  border: 1px solid $border-color;
}
